.select-label {
    color: #f4f4f4;
    font-size: 14px;
    margin-left: 20px;
}

.item {
    display: inline-block;
    color: #202020;
    background: #f4f4f4;
    margin-left: 5px;
    border-radius: 2px;
    font-size: 12px;
    padding: 5px 3px;
    cursor: pointer;
}

.item-active {
    color: blue;
    background: #909090;
}